import Firebase from 'firebase';

const fbRef = new Firebase('https://formbuild.firebaseio.com/');

let authHandler = (error, authData) => {
  if (error) {
    console.log("Login Failed!", error);
  } else {
    console.log("Authenticated successfully with payload:", authData);
  }
}

export function checkAuth() {
  return fbRef.getAuth();
}

export function onAuth() {
  return fbRef.onAuth;
}

export function logout() {
  fbRef.unauth();
}

export function login(authInfo) {
  return new Promise((resolve, reject) => {
    fbRef.authWithPassword({
      email    : authInfo.email,
      password : authInfo.password
    }, (error, authData) => resolve({error, authData}));
  });
}

export function saveForm(data) {
  let authData = checkAuth();
  let fbData = {models: data.models, rendered: data.rendered};
  if (authData) {
    fbRef.child(authData.uid).set(fbData);
  } else {
    fbRef.child('demo').set(fbData);
  }
}



